body{
    background-color: black;
}

h1,h2{
    text-align: center;
    color: white;
}

h1{
    transform: scale(0.5);
}

ul{
    display: flex;
    flex-flow: wrap;
    color: white;

}
.card:nth-child(1){
     background-color:rgb(65, 65, 183);
}
.card:nth-child(2){
    background-color:rgb(49, 143, 65);
}
.card:nth-child(3){
    background-color:rgb(138, 145, 59);
}
.card:nth-child(4){
    background-color:rgb(237, 119, 45);
}
.card:nth-child(5){
    background-color:rgb(65, 65, 183);
}
.card:nth-child(6){
    background-color:rgb(194, 194, 33);
}
.card{
    display: inline;
    width: 23%;
    text-align: center;
    margin: 2%;
    padding: 2%;
    text-align: center;
    font-weight: bold;
}
.card:hover{
    transform:scale(1.2);
} 

span{
    font-size: 10px;
}
.card_small{
    font-size: 200%;
    text-shadow: 5px 5px 6px black;
}


